/* global FB */
import * as React from "react";
import type { HeadFC } from "gatsby";
import Layout from "../components/layout/Layout";
import { navigate, graphql } from "gatsby";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Modal from "../components/modal/modal";
import Tab from "../components/tab/tab";
import ProductCard from "../components/card/Product";
import ListGroup from "react-bootstrap/ListGroup";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import HowItWorks from "../components/section/HowItWorks";

const TabContent = ({ items }) => (
  <div className="row">
    <div className="col-xxl-2 col-xl-2 col-lg-3 col-md-6 col-sm-6">
      <ProductCard data={items[0]} />
      <ProductCard data={items[1]} />
    </div>
    <div className="col-xxl-2 col-xl-2 col-lg-3 col-md-6 col-sm-6 ">
      <ProductCard data={items[2]} />
      <ProductCard data={items[3]} />
    </div>
    <div className="col-xxl-2 col-xl-2 col-lg-3 col-md-6 col-sm-6">
      <ProductCard data={items[4]} />
      <ProductCard data={items[5]} />
    </div>
    <div className="col-xxl-2 col-xl-2 col-lg-3 col-md-6 col-sm-6 ">
      <ProductCard data={items[6]} />
      <ProductCard data={items[7]} />
    </div>

    <div className="col-xxl-2 col-xl-2 col-lg-3 col-md-6 col-sm-6">
      <ProductCard data={items[8]} />
      <ProductCard data={items[9]} />
    </div>
    <div className="col-xxl-2 col-xl-2 col-lg-3 col-md-6 col-sm-6 ">
      <ProductCard data={items[10]} />
      <ProductCard data={items[11]} />
    </div>
  </div>
);

const IndexPage = ({ data }) => {
  const { t } = useTranslation();
  const user = Parse?.User?.current();
  const isAuthenticated = !!user;
  const products = data?.products?.edges?.map((node) => ({
    ...node.node,
  }));
  return (
    <Layout>
      <section className="flash__sell pt-25 pb-40 grey-bg-2">
        <Tab
          title="Hot"
          titleBold="Deals"
          tabObject={{
            groupbuy: <TabContent items={products} />,
          }}
        />
      </section>
    </Layout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }

    products: allProduct {
      edges {
        node {
          id
          media {
            type
            url
          }
        }
      }
    }
  }
`;
