import React, { useState } from "react";

const Component = ({ children, data, extraClass = "" }) => {
  

  const [activeMediaIndex, setActiveMediaIndex] = useState(0);
  const [quantity, setQuantity] = useState(0)
  const price = 20;

  const adjustCart = (offset) => {
    const count = quantity + offset;
    if(count > -1) {
      setQuantity(count)
    }
  }


  const nextMedia = () => {
    setActiveMediaIndex((i) => {
      console.log(i);
      if (i < data?.media?.length - 1) {
        return i + 1;
      } else {
        return 0;
      }
    });
  };

  return (
    <div className="product__item-wrapper mb-20">
      <div className="product__item white-bg">
        {activeMediaIndex + 1} / {data?.media?.length}
        <div className="product__thumb p-relative">
          {data?.media[activeMediaIndex]?.type === "photo" && (
            <div className="w-img" onClick={nextMedia}>
              {data?.media?.length && (
                <img
                  src={data?.media?.length && data?.media[activeMediaIndex].url}
                  alt="product"
                />
              )}
            </div>
          )}
          <div className="w-img">
            {"" && data?.media[activeMediaIndex]?.type === "video" && (
              <video controls>
                <source
                  src={data?.media[activeMediaIndex].url}
                  type="video/mp4"
                />
                Your browser does not support the video tag.
              </video>
            )}
          </div>
        </div>
        <div className="product__content text-center">
          <h6 className="product-name">{data?.title}</h6>
          <span className="new">RM{price}</span>
          <div>
            <div>
              <textarea placeholder="write your order"></textarea>
            </div>
            <div div className="f-left">
            Total: { quantity * price }
            </div>
            
            <div className="f-right">
            <a className="btn btn-primary btn-social">
            <i className="fal fa-minus fa-lg" onClick={() => adjustCart(-1)}></i>
            </a>
            <span>
            {`${quantity}`}
            </span>
            <a className="btn btn-primary btn-social">
            <i className="fal fa-plus fa-lg" onClick={() => adjustCart(1)}></i>
            </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Component;
